* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.App {
  width: 800px;

  .post {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    border: 2px solid teal;
    margin-top: 15px;
  }

  .post-enter {
    transform: translateX(-350px);
  }
  .post-enter-active {
    transform: translateX(0);
    transition: all 500ms ease-in;
  }
  .post-exit {
    opacity: 1;
  }
  .post-exit-active {
    transform: translateX(-350px);
    transition: all 500ms ease-in;
  }

  .post__bnt {
    display: flex;
  }

  .page__wrapper {
    margin-top: 30px;
    display: flex;
  }

  .page {
    padding: 10px;
    border: 1px solid teal;
    cursor: pointer;

    &__current {
      border: 2px solid orange;
      font-weight: bold;
    }
  }
}

.navbar {
  height: 50px;
  width: 100vh;
  display: flex;
  align-items: center;
  padding: 0 15px;
  background-color: lightgray;

  &__links {
    margin-left: auto;

    a {
      margin-left: 20px;
      font-size: 20px;
      font-weight: 700;
      color: black;
      text-decoration: none;
    }

    a:hover {
      color: white;
    }
  }
}

.error {
  margin-top: 50px;
  color: red;
}

.pageID {
  margin: 50px auto;

  &__comments {
    margin-top: 30px;
    margin-bottom: 10px;
  }
}
